import {Component, OnInit} from '@angular/core';
import {EventsService} from "../shared/events.service";
import {ActivatedRoute} from "@angular/router";

@Component({
    templateUrl:'./event-detiles.component.html',
    styles: [
        '.container {padding: 0 20px}' +
        '.event-image {height:100px}'
    ]
})
export class EventDetilesComponent implements OnInit {
    event:any
    
    constructor(private eventServices:EventsService, private route:ActivatedRoute){
    
    }
    ngOnInit(){
     this.event = this.eventServices.getEvent(+this.route.snapshot.params['id']);
    }
}
