import {Routes} from "@angular/router";

import {EventsListComponent} from "../events/events-list.component";
import {EventDetilesComponent} from "../events/event-detiles/event-detiles.component";
import {CreateEventComponent} from "../events/shared/create-event.component";
import {Error404Component} from "../errors/404.component";
import {EventRouteActivator} from "../events/event-detiles/event-route-activator.service";
import {EventListResolveService} from "../events/event-list-resolve.service";

export const appRoute:Routes = [
    {path:'events/new',component: CreateEventComponent, canDeactivate:['canDeactivateCreateEvent']},
    {path:'events',component:EventsListComponent, resolve:{events:EventListResolveService}},
    {path:'events/:id',component: EventDetilesComponent, canActivate:[EventRouteActivator]},
    {path:'404',component: Error404Component},
    {path:'',redirectTo:'/events',pathMatch:'full'}
]

