import {Component, OnInit} from '@angular/core';
import {EventsService} from "./shared/events.service";
import {ToastrService} from "../common/toastr.service";
import {ActivatedRoute} from "@angular/router";


@Component({
    template: `
    <div>
	    <h1>event list</h1>
	    <hr/>
	    <div class="row">
            <div *ngFor="let event of events"  class="col-md-5">
                <event-thumbnail (click)="handleThumbnailClick(event.name)" [event]="event" (eventClick)="handleEventClicked($event)"></event-thumbnail>
            </div>
        </div>
    </div>
    `
})

export class EventsListComponent implements OnInit {
    events: any
    constructor(private eventService: EventsService, private toastr: ToastrService, private route:ActivatedRoute) {
    }
    
    ngOnInit(): void {
        this.events = this.route.snapshot.data.events
    }
    
    handleThumbnailClick(eventName) {
        this.toastr.success(eventName);
    }
    
    handleEventClicked(data) {
        console.log('recived', data);
    }
}
