import {BrowserModule} from '@angular/platform-browser';
import {NgModule} from '@angular/core';

import {EventsAppComponent} from './events-app.component';
import {EventsListComponent} from './events/events-list.component';
import {EventThumbnailComponent} from './events/event-thumbnail.component';
import {EventsService} from './events/shared/events.service';
import {NavBarComponent} from './nav/navbar.component';
import {ToastrService} from "./common/toastr.service";
import {EventDetilesComponent} from "./events/event-detiles/event-detiles.component";
import {RouterModule} from "@angular/router";
import {appRoute} from "./nav/route";
import {CreateEventComponent} from "./events/shared/create-event.component";
import {Error404Component} from "./errors/404.component";
import {EventRouteActivator} from "./events/event-detiles/event-route-activator.service";
import {EventListResolveService} from "./events/event-list-resolve.service";

@NgModule({
    imports: [
        BrowserModule,
        RouterModule.forRoot(appRoute)
    ],
    declarations: [
        EventsAppComponent,
        EventsListComponent,
        EventThumbnailComponent,
        NavBarComponent,
        EventDetilesComponent,
        CreateEventComponent,
        Error404Component
    ],
    providers: [
        EventsService,
        ToastrService,
        EventRouteActivator,
        EventListResolveService,
        {
            provide:'canDeactivateCreateEvent',
            useValue: checkDirtyState
        }
    ],
    bootstrap: [EventsAppComponent]
})
export class AppModule {}
export function checkDirtyState(component:CreateEventComponent){
    if(component.isDirty){
        return window.confirm('clean you stuff')
    }
    return false
}
